.directions {
    position: relative;
    z-index: 3000;

    background-color: @background-blue;
    border-radius: 0 0 20px 20px;

}

.directions__wrapper {
    padding: 0 25px;
    padding-top: 71px;
    padding-bottom: 34px;


}

.directions__title {
    margin: 0;
    margin-bottom: 19px;
    padding: 0 24px;

    color: @special-orange;
}

.directions__text {
    margin: 0;
    margin-bottom: 60px;
    padding: 0 24px;

    color: @special-grey;
}

.directions__country-list {
    margin-bottom: 25px;
}