.social-list {
    margin: 0;
    padding: 0;

    list-style: none;
}

.social-item__link {
    display: block;
    font-size: 0;
    background-repeat: no-repeat;
}

.social-item__link--facebook {
    width: 11px;
    height: 22px;

    background-image: url("../img/icon-facebook.svg");
}

.social-item__link--vk {
    width: 23px;
    height: 12px;

    background-image: url("../img/icon-vk.svg");
}

.social-item__link--instagram {
    width: 18px;
    height: 18px;

    background-image: url("../img/icon-instagram.svg");
}

@media (min-width: @tablet-width) {
    .social-item__link--facebook {
        width: 14px;
        height: 28px;
        background-size: 14px;
    }

    .social-item__link--vk {
        width: 31px;
        height: 16px;
        background-size: 31px;
    }

    .social-item__link--instagram {
        width: 24px;
        height: 24px;
        background-size: 24px;
    }
}