.button {
    display: block;
    position: relative;
    box-sizing: border-box;

    height: 62px;
    width: 100%;
    padding: 22px 23px;

    border-radius: 10px;

    font-size: 18px;
    line-height: 18px;
    font-weight: 700;

    text-decoration: none;

    color: @basic-blue-light;
    background-color: @basic-yellow;

    &::before {
        position: absolute;
        content: "";
        right: 23px;
        top: calc(50% - 9px);

        border: 9px solid transparent;
        border-left: 11px solid @basic-blue-light;
    }

    @media (min-width: @tablet-width) {

        height: 80px;
        padding-top: 29px;
        padding-bottom: 27px;
        padding-left: 33px;

        border-radius: 10px;

        font-size: 24px;
        line-height: 24px;

        &::before {
            position: absolute;
            content: "";
            right: 26px;
            top: calc(50% - 10px);

            border: 10px solid transparent;
            border-left: 12px solid @basic-blue-light;
        }
    }
}