.contacts-list {
    margin: 0;
    padding: 0;
    list-style: none;
}

.contacts-link {
    display: flex;
    text-decoration: none;
    align-items: center;
    column-gap: 16px;
}

.contacts-link__icon-box {
    display: block;
    width: 45px;
    height: 45px;
    background-color: #121936;
    border-radius: 50%;
}

.contacts-link__icon {
    fill: @basic-yellow;
}

.contacts-link__icon--phone {
    width: 16px;
    height: 15px;
    padding: 15px;
}

.contacts-link__icon--email {
    width: 19px;
    height: 13px;
    padding-top: 17px;
    padding-bottom: 15px;
    padding-left: 14px;
    padding-right: 13px;
}

.contacts-link__icon--phone-2,
.contacts-link__icon--email-2 {
    display: none;
}


.contacts-link__text {
    font-size: 16px;
    line-height: 18px;
    font-weight: 700;

    text-transform: uppercase;
    color: #192144;
}

@media (min-width: @tablet-width) {

    .contacts-link {
        column-gap: 23px;
    }

    .contacts-link__icon-box {
        width: 68px;
        height: 68px;
    }

    .contacts-link__icon--phone {
        width: 24px;
        height: 23px;
        padding: 23px 22px;
    }

    .contacts-link__icon--email {
        width: 28px;
        height: 19px;
        padding-top: 26px;
        padding-bottom: 23px;
        padding-left: 21px;
        padding-right: 19px;
    }

    .contacts-link__text {
        font-size: 28px;
        line-height: 28px;
    }
}

@media (min-width: @desktop-width) {

    .contacts-link__icon-box {
        width: 50px;
        height: 50px;
    }

    .contacts-link__icon {
        fill: @basic-blue;
    }

    .contacts-link__icon-box--phone {
        background-color: @special-orange;
    }

    .contacts-link__icon-box--email {
        background-color: @special-yellow-dark;
    }

    .contacts-link__icon--phone,
    .contacts-link__icon--email {
        display: none;
    }

    .contacts-link__icon--phone-2,
    .contacts-link__icon--email-2 {
        display: block;
    }

    .contacts-link__icon--phone-2 {
        padding: 16px;
    }

    .contacts-link__icon--email-2 {
        padding: 18px 17px;
    }

    .contacts-link__text {
        display: none;
    }
}