@media (max-width: @tablet-width-only) {

    .main-nav__site-item,
    .main-nav .main-nav__user-login,
    .main-nav__contacts-item,
    .main-nav__social-item {
        display: none;
    }


}

@media (min-width: @tablet-width) {

    .main-nav .main-nav__user-login {
        position: absolute;
        top: 40px;
        right: 25px;
    }
}

@media (min-width: @desktop-width) {

    .main-nav__wrapper {
        position: relative;
        display: flex;

        padding-left: 100px;

        &::before {
            content: "";
            position: absolute;
            left: 0;
            top: 28px;
            width: 60px;
            height: 2px;

            background-color: #fff;

            //   scroll-menu
            .page-header--scroll & {
                background-color: #192144;
            }
        }
    }

    //   scroll-menu
    & .page-header--scroll .page-header__main-nav a {
        color: #192144;
    }

    .main-nav__social-item,
    .page-header--scroll .main-nav__social-item & {
        display: none;
    }

    .main-nav__site-item {
        padding-top: 21px;
    }

    .main-nav .main-nav__contacts-list {
        display: flex;
        margin-left: auto;
    }

    .main-nav .main-nav__user-login {
        order: 1;
        position: static;
    }
}

// pened-menu and no-js

.page-header--opened-menu,
.page__body--no-js .page-header {
    @media (max-width: @tablet-width-only) {

        & .main-nav__wrapper {
            margin-top: 76px;
            padding-bottom: 9px;
        }

        & .main-nav__site-list {
            margin-bottom: 51px;
        }

        & .main-nav__site-item,
        & .main-nav__contacts-item,
        & .main-nav__social-item {
            display: block;
        }

        & .main-nav__site-item {
            text-align: center;
        }

        & .main-nav__user-login {
            margin-bottom: 51px;
        }

        & .main-nav__contacts-list {
            margin-bottom: 53px;
        }

        & .main-nav__social-list {
            display: flex;
            justify-content: center;
            align-items: center;
            column-gap: 27px;
        }

        @media (min-width: @tablet-width) {
            & .main-nav__wrapper {
                padding: 0 57px;
                padding-bottom: 49px;
                margin-top: 98px;
            }

            & .main-nav__site-list {
                margin-bottom: 144px;
            }

            & .main-nav__site-item {
                text-align: left;
            }

            & .main-nav__contacts-list {
                margin-bottom: 129px;
            }

            .main-nav__social-list {
                padding-left: 26px;
                justify-content: flex-start;
                column-gap: 37px;
            }
        }
    }
}